            <h2>【 자소서 쓰는법 】</h2>
            <br><hr>
            <ul>
                <li class="list"> 자기소개서에서 필수적으로 묻는 항목이 있습니다.</li>
                <li class="list"> 어떻게 살아왔는지 어떤 가치관을 가지고 자라왔는지를 작성 </li>
                <li class="list"> '성장과정'과 자신의 성격에 대한 평가('성격 장단점') </li>
                <li class="list"> 성격을 유추해 볼 수 있는 '취미와 특기'</li>
