        <div class="division">
            <h4>성장과정은 본인의 특별한 점을 부각</h4>
            <p class="p">
                '저는 20xx년도에 태어나 부모와 형제자매...' 이런 식의 틀에 박힌 성장과정은,
                하루에도 수십 수백의 이력서와 자기소개서를 읽는 인사담당자에게는 전혀
