                '독서'와 같은 뻔한 취미 말고 물론 책을 정말 다독한다든지,
                특정 테마의 독서를 많이해서 그것을 회사가 가진 가치관과 연결시킬 수 있다면 써도 상관없습니다.
                이런 경우 정말 취미가 독서인 경우이겠죠.
                활동적이거나, 팀워크를 발휘할 수 있는 취미면 더욱 좋습니다.
                운동 쪽이 좋고 '스카이다이빙' 같은 한두번 경험삼아 해본 액티비티가 아닌
                주기적으로 할 수 있는 것 중에서 단체활동이 포함된 것이
