                입사지원동기는 회사의 비전과 영업활동에 대한 파악이 먼저 있어야 합니다.
                회사는 저마다의 비전, 미션, 목표를 가지고 있습니다.
                회사 홈페이지에 가면 쉽게 확인이 가능합니다.
                그리고 회사의 중장기적인 발전방향은 뉴스나 기타자료 등을 조금만 살펴보면 알 수 있습니다.
