                이 항목의 경우 회사에서 임원이 되겠다는 일반적인 목표도 나쁘지는 않습니다.
                다만 어떤 지원자는 지나치게 솔직해서
                2,3년 경력을 쌓고 더 넓은 곳에서 꿈을 펼치겠다고 하는 분들도 있는데,
                절대 금물입니다.
                회사에 뼈를 묻겠다 정도 까지는 아니더라도 회사에서 많이 배우고,
                회사에서 많이 활용할 수 있는 인재라는 인식을 품게 해야 합니다.
            </p>
        </div>

        <div class="division">
            <h4>절대 지켜야할 것들 - 글자수, 오탈자</h4>
            <p class="p">
                오탈자에 대한 검토는 여러번 하세요.
                자소서 무료첨삭을 하다가 보면,
                줄간격도 맞추지 않고 심지어 오탈자가 한페이지에 4,5개씩 나오는 분들이 계십니다.
                맞춤법을 틀리는 것은 본인의 지식수준을 나타낸다고 한다면,
                오자가 많은 자소서를 그대로 제출한다는 것은 지원 자체의 간절함이 없고
                성의가 없는 지원자임을 나타내는 것입니다.
                오탈자 검토를 하지도 않고 제출하는 것보다는
                차라리 제출하지 않는 것이 낫다고 봅니다.

                글자수에 대한 기준은 반드시 지키세요.
                요즘은 인터넷으로 일정 글자수가 넘어가면 더 이상 입력이 되지 않는 경우가 많습니다.
                그렇다고 1,500자 기준에 500자 정도만 작성하는 등 회사가 정해놓은 글자수에 지나치게
