import { createWebHistory, createRouter } from "vue-router";
import HomeCmt from "@/components/home/HomeCmt.vue";

import IntroCmt from "@/components/intro/IntroCmt.vue";
import ResumeCmt from "@/components/intro/ResumeCmt.vue";
import SelfintroCmt from "@/components/intro/SelfintroCmt.vue";
import MoreCmt from "@/components/intro/MoreCmt.vue";
import BookCmt from "@/components/intro/BookCmt.vue";
import ValuesCmt from "@/components/intro/ValuesCmt.vue";
import GoalsCmt from "@/components/intro/GoalsCmt.vue";

import OtherCmt from "@/components/others/OtherCmt.vue";
import GithubCmt from "@/components/others/GithubCmt.vue";
import TistoryCmt from "@/components/others/TistoryCmt.vue";
import BaekjoonCmt from "@/components/others/BaekjoonCmt.vue";

import ProjectCmt from "@/components/project/ProjectCmt.vue";
import ReactCmt from "@/components/project/ReactCmt.vue";
import VueCmt from "@/components/project/VueCmt.vue";


const routes = [
    {
        path: "/",
        component: HomeCmt,
    },
    {
        path: "/intro",
        component: IntroCmt,
    },
    {
        path: "/intro/resume",
        component: ResumeCmt,
    },
    {
        path: "/intro/selfintro",
        component: SelfintroCmt,
    },
    {
        path: "/intro/more",
        component: MoreCmt,
        children: [
            {
                path: "/intro/more/book",
                component: BookCmt,
            },
            {
                path: "/intro/more/values",
                component: ValuesCmt,
            },
            {
                path: "/intro/more/goals",
                component: GoalsCmt,
            },
        ],
    },
    {
        path: "/project",
        component: ProjectCmt,
    },
    {
        path: "/project/react_project",
        component: ReactCmt,
    },
    {
        path: "/project/vue_project",
        component: VueCmt,
    },
    {
        path: "/others",
        component: OtherCmt,
        children: [
            {
                path: "/others/github",
                component: GithubCmt,
            },
            {
                path: "/others/tistory",
                component: TistoryCmt,
            },
            {
                path: "/others/baekjoon",
                component: BaekjoonCmt,
            },
        ], 
    }
];

const router = createRouter({
    history: createWebHistory(),
    routes
});

export default router;