<template>
    <div class="image-slider">
        <div class="slider-container">
            <img :src="images[currentIndex]" alt="Slider Image" />
        </div>
    </div>
</template>
  
<script>
export default {
    data() {
        return {
            images: [
                require('../../assets/vue1.png'),
                require('../../assets/vue2.png'),
                require('../../assets/vue3.png'),
            ],
            currentIndex: 0,
        };
    },
    mounted() {
        this.startSlider();
    },
    methods: {
        startSlider() {
            setInterval(() => {
                this.currentIndex = (this.currentIndex + 1) % this.images.length;
            }, 3000);
        },
    },
};
</script>
  
<style>
.image-slider {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 15px;
}

.slider-container {
    max-width: 80%;
    max-height: 80%;
}

img {
    width: 100%;
    height: auto;
}
</style>
  