<template>
    <div class="image-slider">
        <div class="slider-container">
            <img :src="images[currentIndex]" alt="Slider Image" />
        </div>
    </div>
</template>
  
<script>
export default {
    data() {
        return {
            images: [
                "https://cdn.pixabay.com/photo/2015/12/04/14/05/code-1076536_1280.jpg",
                'https://cdn.pixabay.com/photo/2019/06/17/19/48/source-4280758_1280.jpg',
                'https://cdn.pixabay.com/photo/2017/08/10/08/47/laptop-2620118_1280.jpg',
                "https://cdn.pixabay.com/photo/2016/09/08/04/12/programmer-1653351_1280.png"
            ],
            currentIndex: 0,
        };
    },
    mounted() {
        this.startSlider();
    },
    methods: {
        startSlider() {
            setInterval(() => {
                this.currentIndex = (this.currentIndex + 1) % this.images.length;
            }, 3000);
        },
    },
};
</script>
  
<style>
.image-slider {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 15px;
}

.slider-container {
    max-width: 80%;
    max-height: 80%;
}

img {
    width: 100%;
    height: auto;
}
</style>
  